
<template>

  <div class="div">

  <div class=" flex-container" >
    <div >
    {{sessionlength}} + {{breaklength}} Clock
    </div>
  </div>
    <div class=" flex-container dl">
        <div class="item2">
    Break Length<br>
    <b-icon-arrow-up @click=breakmore()></b-icon-arrow-up>
    {{breaklength}}
    <b-icon-arrow-down @click=breakless()></b-icon-arrow-down>
    </div>
        <div class="item2">
    Session Length<br>
    <b-icon-arrow-up  @click=sessionmore()></b-icon-arrow-up>
    {{sessionlength}}
    <b-icon-arrow-down @click=sessionless()></b-icon-arrow-down>
    </div>
    </div>
    <div class=" flex-container dl">
      <div class="box1">
      <h3>{{type}}</h3>
      <p :class="{red: lastminute}">{{timeminute}} : {{timeseconds}}</p>
      </div>
    </div>

        <div class=" flex-container1 dl">
      <div class="box2">
      <b-icon-play  @click=play()></b-icon-play>
      <b-icon-pause @click=pause()></b-icon-pause>
      <b-icon-arrow-clockwise @click=reinitglobal()></b-icon-arrow-clockwise>
      </div>
    </div>

            <div class=" flex-container1 dl">
      <div class="box3">
<p>Designed and coded by Octave DESCOURS</p>
      </div>
    </div>
    {{nowdate}}
    {{seconds}}
    <p>{{time}}</p>



  </div>

</template>





<script>
export default {
  name: 'HelloWorldBis',
    data() {
        return {
          sessionlength:25,
          breaklength:5,
          nowdate: new Date(),
          seconds: (new Date()).getSeconds(),

          timeminute: 25,
          timeseconds: "00",
          isRunning: false,
          interval: null,
          time: 0,
          dejaplay: false,
          type: "Session",
          lastminute: false,
  }
},

props: {
},

methods: {
  breakmore(){
    if (this.breaklength>1){
    this.breaklength=this.breaklength+1}
  },
  breakless(){
    if (this.breaklength>1){
    this.breaklength=this.breaklength-1}
  },
      sessionmore(){
        if (this.sessionlength>1){
    this.sessionlength=this.sessionlength+1
    this.timeminute=this.sessionlength
    this.timeseconds="00"
    this.reinit()}

  },
    sessionless(){
      if (this.sessionlength>1){
    this.sessionlength=this.sessionlength-1
    this.timeminute=this.sessionlength
    this.timeseconds="00"
    this.reinit()}
  },



    play() {
      if (this.isRunning==false && this.dejaplay==false) {
        this.time = parseInt(this.timeminute*60) + parseInt(this.timeseconds);
        this.interval = setInterval(this.incrementTime, 1000);
        this.isRunning = !this.isRunning;
        this.dejaplay=true;
      }
      else if (this.isRunning==false && this.dejaplay==true){
        this.interval = setInterval(this.incrementTime, 1000);
        this.isRunning = !this.isRunning;
      }
    },
    
    incrementTime() {
      if((parseInt(this.time))==0){
        if (this.type=="Session"){
          this.type ="Break";
          this.time =  parseInt(this.breaklength*60 +1)
          }
        else if(this.type=="Break"){
          this.type="Session";
          this.time =  parseInt(this.sessionlength*60 +1)}
      }
      this.time = parseInt(this.time) - 1;
      this.timeminute = Math.floor(this.time/60);
      if(parseInt(this.time % 60)<10){ 
      this.timeseconds = "0" + String(this.time % 60)}
      else {this.timeseconds = this.time % 60}
      if(Math.floor(this.time/60)==0){this.lastminute=true}
      else{this.lastminute=false}
    },

    pause(){
            if (this.isRunning) {
        clearInterval(this.interval);
        this.isRunning = !this.isRunning
      }
    },

        reinit(){
          this.pause(),
          this.interval= null,
          this.time= 0,
          this.dejaplay= false
        },

        reinitglobal(){
          
          this.pause(),
          this.interval= null,
          this.time= 0,
          this.dejaplay= false,
          this.timeminute= 25,
          this.timeseconds= "00",
          this.sessionlength= 25,
          this.breaklength= 5
        }

}
}

</script>

